:root {
  --changeColor: changeColor 1s ease 0.1ms infinite reverse;
  --changeColorReverse: changeColor 3s ease 0.5ms 20 alternate;
  --move: move 0.8s ease 0.3s infinite reverse;
}

@keyframes changeColor {
  1% {
    color: rgba(212, 201, 100, 0.616);
  }
  15% {
    color: rgba(57, 211, 57, 0.452);
  }
  30% {
    color: rgba(105, 105, 194, 0.733);
  }
  40% {
    color: rgba(255, 0, 242, 0.452);
  }
  60% {
    color: rgba(207, 110, 194, 0.219);
  }
  75% {
    color: rgba(57, 216, 51, 0.61);
  }
  85% {
    color: rgba(51, 122, 216, 0.829);
  }
}

body,
html {
  margin: 0;
}
.tituloIntro {
  background: none;
  color: #4ba738;
  text-decoration: none;
  width: 600px;
  height: 100px;
  margin: auto;
  text-align: center;
  font-size: 30px;
  letter-spacing: 3px;
  word-spacing: 25%;
  line-height: 100px;
  animation: var(--changeColor);
}
.firstIntro {
  transition: 1s ease;
  font-weight: 500;
  animation: var(--changeColorReverse);
}
.firstIntro:hover {
  background: white;
  font-size: 30px;
  letter-spacing: 3px;
  word-spacing: 25%;
  animation: var(--changeColor);
  transition: 3s ease 0.3ms;
}
