:root {
  --width: 300px;
  --pading: 10px;
  --bgcolor: rgb(0, 0, 0);
  --hovercolor: rgba(18, 121, 124, 0.425);
  --width-collapsed: 50px;
}
body {
  background-color: #ffffff;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 18px;
  padding: 0;
  margin: 0;
  margin-left: var(--width-collapsed);
}
.body-expanded {
  margin-left: var(--width);
}
#main-container {
  color: white;
}
#sidemenu {
  background-color: var(--bgcolor);
  color: white;
  position: fixed;
  height: 100%;
  left: 0;
  top: 0;
}
#sidemenu a {
  color: #fefefe;
  text-decoration: none;
}

/*
HEADER
*/

#sidemenu #header {
  box-sizing: border-box;
  border-bottom: solid 1px #4ba738; /*se cambia el color de las lineas*/
}
#sidemenu #header #title {
  box-sizing: border-box;
  overflow: hidden;
  text-align: center;
}
#sidemenu #header #title,
#sidemenu #header #menu-btn {
  vertical-align: middle;
}
#sidemenu #header #menu-btn {
  display: inline-block;
}
#sidemenu #header #menu-btn {
  cursor: pointer;
  padding: 10px;
  width: var(--width-collapsed);
  box-sizing: border-box;
}
#sidemenu #header #menu-btn:hover {
  background-color: var(--hovercolor);
}
#sidemenu #header #menu-btn .btn-hamburger {
  background-color: #fff;
  width: 100%;
  height: 2px;
  margin: 5px 0;
}
/*
PROFILE
*/
#sidemenu #profile {
  border-bottom: solid 1px #4ba738;
  padding: var(--pading) 0;
  text-align: center;
}
#sidemenu #profile #photo {
  box-sizing: border-box;
  padding: var(--pading);
  margin: 0 auto;
}
#sidemenu #profile #photo img {
  border-radius: 50%;
  width: 60%;
}
#sidemenu #profile #photo,
#sidemenu #profile #name {
  font-size: 24px;
  padding: var(--pading) 0;
  overflow: hidden;
}
/*
ITEM
*/
#sidemenu #menu-items {
  overflow: hidden;
}
#sidemenu #menu-items .item {
  width: var(--width);
}
#sidemenu #menu-items .item .icon {
  display: inline-block;
}
#sidemenu #menu-items .item .icon,
#sidemenu #menu-items .item .title {
  font-size: 14px;
  vertical-align: middle;
  overflow: hidden;
  display: inline-block;
}

#sidemenu #menu-items .item a {
  display: block;
}
#sidemenu #menu-items .item a:hover {
  background-color: var(--hovercolor);
}
#sidemenu #menu-items .item .icon {
  box-sizing: border-box;
  padding: var(--pading);
  width: var(--width-collapsed);
}
#sidemenu #menu-items .item .icon img {
  width: 100%;
}
#sidemenu #menu-items .item .title {
  padding: var(--pading);
}
#sidemenu #menu-items .item.separator {
  height: 1px;
  border-bottom: solid 1px #000000;
  margin: 15px 0;
}

.menu-expanded {
  width: var(--width);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}
.menu-expanded #header #title {
  display: inline-block;
  width: calc(100% - 50px);
  margin-right: -5px;
  padding: var(--pading);
}
.menu-expanded #header #title span {
  width: calc(var(--width) - var(--width-collapsed) - 5px);
  overflow: hidden;
}

.menu-expanded #profile #photo {
  width: 200px;
}
.menu-expanded #profile #name {
  width: 100%;
}

.menu-collapsed {
  width: var(--width-collapsed);
}
.menu-collapsed #header #title {
  width: 0;
  height: 0;
  padding: 0;
  margin: 0;
}
.menu-collapsed #profile #name {
  display: none;
}
.menu-collapsed .item {
  width: 100px;
}
